//PopupWindow.js
import React, { useState, useEffect } from "react";
import InitialButton from "./InitialButton";
import Popup from "./Popup-01"; // 2023/9/19-02-01
import TabbedData from "./TabbedData-07";

const PopupWindow = () => {
  const [showInitButton, setShowInitButton] = useState(true);
  const [showPopup, setShowPopup] = useState(false);
  const [showSetting, setShowSetting] = useState(false);
  const [isEaRunning, setIsEaRunning] = useState(false);  //EA
  const [setReceivedTabData] = useState([]);
  
  const handleFileInitialClick = () => {
    //Popup, Init
    setShowInitButton(false);
    setShowPopup(true);
  };

  // const handleShowPopup = () => {
  //   //Popup
  //   setShowPopup(true);
  // };

  const handleClosePopup = () => {
    //Popup
    setShowPopup(false);
  };

  const handleSettingTabbelData = () => {
    //Popup
    setShowPopup(false);
    setShowSetting(true);
  };

  const handleExitSettingsClick =() =>{
    //Popup
    setShowSetting(false);
    setShowPopup(true);
  };

  const handleStartEAClick = (data) =>{
    //
    setReceivedTabData(data);
    console.log(' TabData = ',data);
    setIsEaRunning(true);
    setShowSetting(false);
    setShowPopup(true);
  }

  const handleCloseEA = () =>{
    //ea
    setIsEaRunning(false);
    setShowInitButton(false);
  }

  useEffect(() => {
    if ( showPopup || showSetting) {
      setShowInitButton(false);
    } else if(!showPopup && !showSetting) {
      setShowInitButton(true);
    }
  }, [showPopup, showSetting, isEaRunning]);


  return (
    <div>
      {showInitButton && 
        <InitialButton show={showInitButton} showEA={isEaRunning} onInitClick={handleFileInitialClick} />}
      {showPopup && 
        <Popup show={showPopup} showEA={isEaRunning} onClose={handleClosePopup} onCloseEA={handleCloseEA} onSetData={handleSettingTabbelData} />}
      {showSetting && 
        <TabbedData show={showSetting} onExitSettingsClick={handleExitSettingsClick}  onStartEAClick={handleStartEAClick} />}
    </div>
  );
};

export default PopupWindow;