import React, { useState } from "react";
import Draggable from "react-draggable";
import Button from "@mui/material/Button";
import Box from "@mui/material/Box";
import Typography from "@mui/material/Typography";
import TextField from "@mui/material/TextField";
//import Collapse from '@mui/material/Collapse';
import NewComponent from "./commonParts/NewComponent";
import TypographyInfo from "./commonParts/TypographyInfo";
import PinnedSubheaderList from "./commonParts/PinnedSubheaderList";

const Popup = ({ show, onClose, onSetData, onCloseEA, showEA }) => {
  const [windowScale, setWindowScale] = useState(100);
  const [isExitEaPopupOpen, setIsExitEaPopupOpen] = useState(false);
  const [isCloseOrdersPopupOpen, setIsCloseOrdersPopupOpen] = useState(false);
  const [isCloseOrdersInfoOpen, setIsCloseOrdersInfoOpen] = useState(false);

  const gap = 6;
  const rightgap = 3;
  const buttonWidth = 260;
  const buttonHeight = 38;
  const buttonWidthGap = 46;
 
  //
  const handleScaleInputChange = (e) => {
    const value = Number(e.target.value);
    if (value >= 30 && value < 300) {
      setWindowScale(value);
    }
  };


  const handleMinimize = () => {
    onClose();
  };


  const handleSetSymbolParameters = () =>{
    //TabbedData.js
    onSetData();
    onClose();
  }


  //EA
  const handleExitIntelligentTrading = () => {
    setIsExitEaPopupOpen(true);
   }
    // 
    const confirmTextExit = 'Are you sure you want to exit intelligent trading?'
    // '' 
    const handleIsCancelExit = () => {
      setIsExitEaPopupOpen(false);
      // 
    };
    // ''
    const handleIsConfirmExit = () => {
      setIsExitEaPopupOpen(false);
      // 
      handleConfrimExit();
    };
    //
    const handleConfrimExit = () => {
      onClose();
      onCloseEA();
  };  

  //
  const handleCloseAllOrders = () =>{
    setIsCloseOrdersPopupOpen(true);
    }
    // 
    const confirmTextCloseAll = 'Are you sure you want to close and delete all orders?'
    // '' 
    const handleIsCancelCloseAll = () => {
      setIsCloseOrdersPopupOpen(false);
      // 
    };
    // ''
    const handleIsConfirmCloseAll = () => {
      setIsCloseOrdersPopupOpen(false);
      // 
      handleConfrimCloseAll();
    };
    // 
    const handleConfrimCloseAll = () => {
      setIsCloseOrdersInfoOpen(true);
      setTimeout(() => {
        setIsCloseOrdersInfoOpen(false);
      }, 3000);
      // 
      // 
  };

  //const items = ["Item 1", "Item 2", "Item 3", "Item 4", "Item 5", "Item 6"];

  //disabled={true}  //true
  return (
    <div color="black">
        
      {show && (
        <Draggable  disabled={windowScale === 100 ? true: false}> 

            <Box 
              sx={{
                position: "fixed",
                top:  "0%",  //"5%",
                left: '0%',  //"5%",
                width: `${windowScale}vw`,
                height: `${windowScale}vh`,
                background: `linear-gradient(135deg, #B8D26D, #5B8930)`,
                //zIndex: 9999,
              }}
              >
              <Typography
                sx={{
                  textAlign: "center",
                  margin: gap / 3,
                }}
                variant="h4"
                >
                Intelligent Trading Online
              </Typography>
              <TextField
                type="number"
                value={windowScale}
                onChange={handleScaleInputChange}
                inputProps={{
                  style: { textAlign: "center" },
                  min: 30,
                  max: 300,
                }}
                sx={{
                  position: "absolute",
                  top: "10px",
                  left: "10px",
                  width: "40px",
                }}
                />
              <TypographyInfo windowScale={windowScale} />
              <Button
                onClick={handleMinimize}
                sx={{
                  background: `linear-gradient(135deg, #B8D26D, #5B8930)`,
                  position: "absolute",
                  top: `${gap}px`,
                  right: `${rightgap}px`,
                  width: `${gap}px`,
                }}
                >
                  -
              </Button> 
            
              {isCloseOrdersInfoOpen && (
                <TypographyInfo showText={'You are closing all holding orders and deleting all pending orders!'} />
              )}
              <Button 
                onClick={handleCloseAllOrders}
                sx={{
                  background: `linear-gradient(135deg,  #d28a6d, #895130)`,  //
                  position: "absolute",
                  top: `${gap * 1 + buttonWidthGap *1}px`,  // 
                  right: `${rightgap}px`,
                  //margin: `${rightgap}px`,
                  width : `${buttonWidth}px`,
                  height  : `${buttonHeight}px`,
                }}
              >
                Closs And Delete All Orders
              </Button> 
              {isCloseOrdersPopupOpen && (
                <NewComponent 
                  onConfirm={handleIsConfirmCloseAll} 
                  onCancel={handleIsCancelCloseAll} 
                  confirmText={confirmTextCloseAll}
                />
              )}

              <Button 
                onClick={handleSetSymbolParameters}
                sx={{
                  background: `linear-gradient(135deg,   #B8D26D, #5B8930)`,  
                  position: "absolute",
                  top: `${gap * 1 + buttonWidthGap*2}px`,  // 
                  right: `${rightgap }px`,
                  width : `${buttonWidth}px`,
                  height  : `${buttonHeight}px`,
                }}
              >
                Set Symbol Parameters
              </Button> 
              
              <div style ={{ 
                  display: "flex" ,
                  //margin: "20px",
                  position: "absolute",
                  top: `${gap * 1 + buttonWidthGap*3 }px`,  // 
                  width : `${buttonWidth-3}px`,
                  right: `${rightgap  }px`,
                  margin: `${rightgap  }px`,
                  //marginLeft:  "calc(100% - 56px)", // 
                }}
                >
                {/* Place other components items={items}*/}
                <PinnedSubheaderList  />
              </div>


              <Button
                onClick={handleExitIntelligentTrading} 
                sx={{
                  background: `linear-gradient(135deg, #B8D26D, #5B8930)`,
                  position: "absolute",
                  bottom: `${gap}px`,
                  right: `${rightgap}px`,
                  width : `${buttonWidth}px`,
                  height  : `${buttonHeight}px`,
                }}
                >
                  Exit Intelligent Trading
              </Button>
              {isExitEaPopupOpen && (
                <NewComponent 
                  onConfirm={handleIsConfirmExit} 
                  onCancel={handleIsCancelExit} 
                  confirmText={confirmTextExit}
                />
              )}
            </Box>
        </Draggable>
      )}
    </div>
  );
};

export default Popup;