//InputsFund.js
//Inputs-v05
//
import React, { useState, useMemo } from 'react';
import { TextField, Button, Grid, Box, Typography } from "@mui/material";
import annotationsFund from './commonParts/annotationsFund';

export let newInputFund = [];   //['InputsFund','00:00','23:59','10','2.0','500','-500']; 

const InputsFund = ({ onClick }) => {
  const initialInputValues = useMemo(() => Array(6).fill(""), []);
  const [inputValues, setInputValues] = useState(initialInputValues);
  const [inputRules] = useState([
    /^([01][0-9]|2[0-3]):[0-5][0-9]$/, //1  --
    /^([01]?[0-9]|2[0-3]):[0-5][0-9]$/, //1  --
    /^[0-9]+$/, // 2 ---
    /^[0-9]+(\.[0-9])?/, // 3 - 
    /^[0-9]+(\.[0-9]+)?/, // 3 - 
    /^[0-9]+(\.[0-9]+)?/, // 3 - 
    // ... 
  ]);

  const handleInputChange = (event, index) => {
    const { value } = event.target;

    if (!validateInput(value, inputRules[index])) {
      event.preventDefault();
      return;
    }

    setInputValues((prevValues) => {
      const updatedValues = [...prevValues];
      updatedValues[index] = value;
      return updatedValues;
    });
  };

  const handleKeyDown = (event, index) => {
    if (
      (event.key === "Backspace" || event.key === "Delete") &&
      inputValues[index].length === 1
    ) {
      setInputValues((prevValues) => {
        const updatedValues = [...prevValues];
        updatedValues[index] = "";
        return updatedValues;
      });
    }
  };

  const handleConfirmation = () => {
    //
    //saveDataToLocalStorage(inputValues)
    newInputFund = [...inputValues];
    //console.log('Inputs.js  inputValues = ',newInputFund);
    // onClick
    onClick && onClick();
    //
    setInputValues(initialInputValues);
  };


  const handleReset = () => {
    setInputValues(initialInputValues);
    //console.log("");
  };

  const validateInput = (value, rule) => {
    if (!rule) {
      return true; // 
    }

    const regex = new RegExp(rule);
    return regex.test(value);
  };

  const xs = 6;
  const sm = 3;

  return (
    <Box
      display="flex"
      flexDirection="column"
      alignItems="center"
      marginTop={3}
      textAlign="left"  //"center" // 
    >
      <Typography variant="h5" component="h2">
        Set fund management parameters for the account
      </Typography>
      <br />{/*  */}
      <Box marginTop={2}>
        <Grid container spacing={3}>
          <Grid item xs={xs} sm={sm}>
            <TextField
              label={annotationsFund[0]}   //""
              type="time"
              value={inputValues[0]}
              onChange={(event) => handleInputChange(event, 0)}
              onKeyDown={(event) => handleKeyDown(event, 0)} 
              sx={{ width: '200px' }}
            />
          </Grid>
          <Grid item xs={xs} sm={sm}>
            <TextField
              label={annotationsFund[1]}  //""
              type="time"
              value={inputValues[1]}
              onChange={(event) => handleInputChange(event, 1)}
              onKeyDown={(event) => handleKeyDown(event, 1)} 
              sx={{ width: '200px' }}
            />
          </Grid>
          <Grid item xs={xs} sm={sm}>
            <TextField
              label={annotationsFund[2]}  //""
              type="text"
              value={inputValues[2]}
              onChange={(event) => handleInputChange(event, 2)}
              onKeyDown={(event) => handleKeyDown(event, 2)} />
          </Grid>
          <Grid item xs={xs} sm={sm}>
            <TextField
              label={annotationsFund[3]} //""
              type="text"
              value={inputValues[3]}
              onChange={(event) => handleInputChange(event, 3)}
              onKeyDown={(event) => handleKeyDown(event, 3)} />
          </Grid>
          <Grid item xs={xs} sm={sm}>
            <TextField
              label={annotationsFund[4]} //""
              type="text"
              value={inputValues[4]}
              onChange={(event) => handleInputChange(event, 4)}
              onKeyDown={(event) => handleKeyDown(event, 4)} />
          </Grid>
          <Grid item xs={xs} sm={sm}>
            <TextField
              label={annotationsFund[5]} //""
              type="text"
              value={inputValues[5]}
              onChange={(event) => handleInputChange(event, 5)}
              onKeyDown={(event) => handleKeyDown(event, 5)} />
          </Grid>
          
          <Grid item xs={xs}>
            <Button
              onClick={handleConfirmation}
              disabled={inputValues.some(value => value === '')}
              variant="contained"
            >
              Confirm Inputs
            </Button>
            <Button
              onClick={handleReset}
              disabled={!inputValues.some(value => value !== '')}
              variant="contained"
              sx={{ marginLeft: "1rem" }}
            >
              Reset Inputs
            </Button>
          </Grid>
        </Grid>
      </Box>
    </Box>
  );
};

export default InputsFund;